<template>
  <div>
	<el-row style="margin-bottom:20px;">
	    <el-card shadow="always">
	    	<span class="content-title">咨询管理</span>
			<el-button type="success" @click="openAddDialog()">添加</el-button>
	    </el-card>
	</el-row>
	<el-table
		border
		:data="tableData"
	>
		<el-table-column
			prop="index"
			label="序号"
			align="center"
			width="80">
		</el-table-column>
		<el-table-column
			prop="company_name"
			label="公司名称"
			align="center">
		</el-table-column>
		<el-table-column
			prop="company_address"
			label="公司地址"
			align="center">
		</el-table-column>
		<el-table-column
			prop="name"
			label="联系人姓名"
			align="center"
			width="120">
		</el-table-column>
		<el-table-column
			prop="mobile"
			label="联系人号码"
			align="center"
			width="130">
		</el-table-column>
		<el-table-column
			prop="mail"
			label="联系人邮箱"
			align="center"
			width="180">
		</el-table-column>
		<el-table-column
			prop="create_time"
			label="创建时间"
			align="center"
			width="200">
		</el-table-column>
		<el-table-column
			label="操作"
			align="center"
			width="100">
			<template slot-scope="scope">
		        <el-button
		          size="mini"
		          type="primary"
		          @click="handleEdit(scope.$index, scope.row)">详情</el-button>
		    </template>
		</el-table-column>
	</el-table>
	<div class="block pager">
	  <el-pagination
	      @size-change="handleSizeChange"
	      @current-change="handleCurrentChange"
	      :current-page.sync="currentPage"
	      :page-sizes="[15, 20, 30]"
	      :page-size="page_size"
	      layout="sizes, prev, pager, next"
	      :total="total">
	  </el-pagination>
	</div>
	<el-dialog
	  title="添加"
	  :visible.sync="dialogVisible"
	  width="50%"
	  :append-to-body='true'>
	  <div class="el-dialog-counsel">
	  <el-form label-position="top" :rules="rules" ref="formLabelAlign" label-width="80px" :model="formLabelAlign">
	  	  <el-form-item label="公司名称" prop="company_name">
			<el-input v-model="formLabelAlign.company_name"></el-input>
		  </el-form-item>
		  <el-form-item label="公司地址" prop="company_address">
			<el-input v-model="formLabelAlign.company_address"></el-input>
		  </el-form-item>
		  <el-form-item label="公司简介" prop="company_summary">
			  <el-input
				  type="textarea"
				  v-model="formLabelAlign.company_summary"
				  :autosize="{ minRows: 10, maxRows: 33}"
				  placeholder="请输入内容">
			  </el-input>
		  </el-form-item>
		  <el-form-item label="行业" prop="industry">
			<el-input v-model="formLabelAlign.industry"></el-input>
		  </el-form-item>
		  <!--<el-form-item label="活动性质">
		    <el-checkbox-group v-model="formLabelAlign.business">
		   	  <el-checkbox v-for="json in dictionary_list" :label="json.id" name="business">{{json.name}}</el-checkbox>
		    </el-checkbox-group>
		  </el-form-item>-->
		  <el-form-item label="联系人姓名" prop="name">
			<el-input v-model="formLabelAlign.name"></el-input>
		  </el-form-item>
		  </el-form-item>
		  <el-form-item label="联系人号码" prop="mobile">
			<el-input type="number" v-model="formLabelAlign.mobile"></el-input>
		  </el-form-item>
		  <el-form-item label="联系人邮箱" prop="mail">
			<el-input v-model="formLabelAlign.mail"></el-input>
		  </el-form-item>
	  </el-form>
	  </div>
	  <span slot="footer" class="dialog-footer" v-if="!formLabelAlign.hasOwnProperty('con_id')">
	    <el-button @click="cancleDialog">取 消</el-button>
	    <el-button type="primary" @click="submitDialog('formLabelAlign')">提 交</el-button>
	  </span>
	</el-dialog>
  </div>
</template>

<script>
import axios from 'axios';
import { utcTimeChange } from '@/utils';
export default {
	data() {
	  var validateMobile = (rule, value, callback) => {
	  	var reg=11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
	  	if (value === '') {
          callback(new Error('请输入手机号'));
        } else if(!reg.test(this.formLabelAlign.mobile)){
	  		callback(new Error('手机号格式不正确'));
	  	}else {
          callback();
        }
      };
      var validateEmail = (rule, value, callback) => {
	  	var regEmail=new RegExp("^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$");
	  	if(value != '' && !regEmail.test(this.formLabelAlign.mail)){
	  		callback(new Error('邮箱格式不正确'));
	  	}else {
          callback();
        }
      };
      return {
      	dictionary_list:[],
        selectClass:"el-icon-arrow-down",
        content_show:false,
        //表格
        tableData: [],
		currentPage:1,
		page_size:15,
		total:0,
		//弹出框
		dialogVisible: false,
		//表单
		formLabelAlign: {
          name: '',
          mobile:'',
          mail: '',
          company_name:'',
          company_address:'',
          company_summary:'',
          industry:'',
          business:[]
        },
        rules: {
          name: [{ required: true, message: '请输入姓名', trigger: 'blur' }],
          mobile: [{ required: true, message: '请输入手机号', trigger: 'blur' },
          	{ validator: validateMobile, trigger: 'blur' }],
          mail :[{ validator: validateEmail, trigger: 'blur' }]
        }
      };
    },
    mounted() {
    	//this.getDictionary();
    	this.getcounselList("1","15");
    },
    methods: {
      getDictionary(){
      	var that = this;
      	var params = {code:'business'};
      	axios.post('core/dictionary/list', params).then(function(response){
			var code = response.code;
			if(0 == code){
				that.dictionary_list = response.data;
			}else{
			}
		},function(error){
		});
      },
      handleSizeChange(val) {
        this.getcounselList(this.currentPage,val);
      },
      handleCurrentChange(val){
      	this.getcounselList(val,this.page_size);
      },
      //详情
      handleEdit(index, row) {
	        this.dialogVisible = true;
	        this.formLabelAlign = row;
	  },
      openAddDialog(){
      	this.formLabelAlign = {
      	  name: '',
          mobile:'',
          mail: '',
          company_name:'',
          company_address:'',
          company_summary:'',
          industry:'',
          business:[]
        };
      	this.dialogVisible = true;
      },
      submitDialog(formType){
      	var that = this;
      	this.$refs[formType].validate(valid => {
	        if (valid) {
	        	that.dialogVisible = false;
	        	var method = '';
	        	var info = '';
	        	if(that.formLabelAlign.hasOwnProperty("con_id"))
	        	{
	        		method = 'core/user/update';
	        		info = '修改成功!';
	        	}else{
	        		method = 'content/consultative/create';
	        		info = '添加成功!';
	        	}
	        	that.submitUserInfo(method,that.formLabelAlign,info);
	        } else {
	          return false;
	        }
	    });
      },
      cancleDialog(){
      	this.dialogVisible = false;
      },
      getcounselList(page_index,page_size){
      	var params = {page_index:page_index,page_size:page_size};
      	var that = this;
		axios.get('content/consultative/pageList', {params}).then(function(response){
			var code = response.code;
			if(0 == code){
				var array = [];
				var rdata = response.data;
				var count = rdata.count;
				var page_index = rdata.page_index;
				var page_size = rdata.page_size;
				that.currentPage = page_index;
				that.page_size = page_size;
				that.total = count;
				var des_num = (page_index - 1) * page_size;
				var json = rdata.rows;
				for(var i = 0;i < json.length;i++){
					json[i].index = des_num + i + 1;
					array.push(json[i]); 
				}
				that.tableData = array;
			}else{
				//console.log(code);
			}
		},function(error){
			//console.log(error);
		});
      },
      submitUserInfo(method,params,info){
      	var that = this;
		axios.post(method, params).then(function(response){
			var code = response.code;
			if(0 == code){
				that.$message({
		            type: 'success',
		            message: info
		        });
				that.getcounselList(that.currentPage,that.page_size);
			}else{
				that.$message({
		            type: 'success',
		            message: '操作失败:' + response.message
		        });
			}
		},function(error){
		});
      }
    }
};
</script>

<style scoped>
.content-title{
	margin-right:20px;
}
.advanced-box{
	padding:20px 10px;
	margin-top:15px;
	background-color:#f7f7f7;
}
