	border-bottom: 1px solid #CCC;
}
.el-dialog-counsel{
	height:530px !important;
	padding-right:15px;
	overflow-y:scroll;
}
.el-dialog__footer{
	border-top: 1px solid #CCC;
}
.el-form-item__label{
	padding-bottom:0 !important;
}
</style>